// more info: https://codepen.io/collection/nLbRMZ/
// 
@mixin fluid-type($properties, $min-vw, $max-vw, $min-value, $max-value) {
  & {
    @each $property in $properties {
      #{$property}: $min-value;
    }
    @media screen and (min-width: $min-vw) {
      @each $property in $properties {
        //#{$property}: calc(#{$min-value} + #{strip-unit($max-value - $min-value)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)}));
        #{$property}: calc-interpolation($min-vw, $min-value, $max-vw, $max-value);
      }
    }
    @media screen and (min-width: $max-vw) {
      @each $property in $properties {
        #{$property}: $max-value;
      }
    }
  }
}

// Requires the calc-interpolation function which can also be used independently
@function calc-interpolation($min-screen, $min-value, $max-screen, $max-value) {
  $a: ($max-value - $min-value) / ($max-screen - $min-screen);
  $b: $min-value - $a * $min-screen;
  $sign: "+";
  @if ($b < 0) {
    $sign: "-";
    $b: abs($b);
  }
  @return calc(#{$a*100}vw #{$sign} #{$b});
}

@function strip-unit($value) {
  @return $value / ($value * 0 + 1);
}

@mixin center-content {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3D(-50%, -50%, 0);
}

@mixin fw() {
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 0;
}

// offset cols will be pushed back in the next bootstrap 4 beta 
// for now here they are
@mixin make-col-offset($size, $columns: $grid-columns) {
  margin-left: percentage($size / $columns);
}

@mixin make-col-offset-abs($size, $columns: $grid-columns) {
  left: percentage($size / $columns);
}

@mixin full-size-absolute() {
  @include fw();
  position: absolute;
}

@mixin full-size-fixed() {
  @include fw();
  position: fixed;
}

@mixin full-size-overlay() {
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  z-index: 1;
}


/*
	Examples
	.class-1 { @include aspect-ratio(); } 				No arguments defaults to a 1:1 ratio
	.class-2 { @include aspect-ratio(16, 9); } 			Width and height
	.class-3 { @include aspect-ratio(1.777778); } 		Ratio (calculated width/height)
	.class-4 { @include aspect-ratio(4px, 3px); } 		Comparable units
	.class-5 { @include aspect-ratio($ratio: 1.2); } 	Keywords
*/

@mixin aspect-ratio($ratio-or-width, $height: null) {
  $padding: if($height, percentage($height/$ratio-or-width), percentage(1/$ratio-or-width));
  &:before {
    content: '';
    float: left;
    padding-bottom: $padding;
  }
  &:after {
    content: '';
    display: table;
    clear: both;
  }
}

// Portrait
@mixin portrait() {
  @media screen and (orientation: portrait) {
    @content;
  }
}

@mixin media-portrait-up($name) {
  @include portrait {
    @include media-breakpoint-up($name) {
      @content;
    }
  }
}

@mixin media-portrait-down($name) {
  @include portrait {
    @include media-breakpoint-down($name) {
      @content;
    }
  }
}

// Landscape
@mixin landscape() {
  @media screen and (orientation: landscape) {
    @content;
  }
}

@mixin media-landscape-up($name) {
  @include landscape {
    @include media-breakpoint-up($name) {
      @content;
    }
  }
}

@mixin media-landscape-down($name) {
  @include landscape {
    @include media-breakpoint-down($name) {
      @content;
    }
  }
}